@font-face {
  font-family: QuickSand;
  src: url("Quicksand-VariableFont_wght.ce1d45b9.ttf");
}

@font-face {
  font-family: Raleway;
  src: url("Raleway-VariableFont_wght.395f909d.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Raleway, monospace;
}

:root {
  --color-1: #18191a;
  --color-2: #242526;
  --color-3: #3a3b3c;
  --color-4: #e4e6eb;
  --color-5: #b0b3b8;
  --color-6: #36454f;
}

body {
  background-color: var(--color-1);
  width: 100%;
  font-size: 16px;
}

header {
  background-color: var(--color-2);
  border-bottom: .1px solid var(--color-6);
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

main, .viewheight65 {
  height: 65vh;
}

footer {
  background-color: var(--color-3);
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
  display: flex;
}

.stats-row {
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

nav ul {
  align-items: center;
  list-style: none;
  display: flex;
}

.container-logo {
  color: #fff;
  align-items: center;
  gap: 4px;
  display: flex;
}

.logo {
  width: auto;
  height: 60px;
}

.icon-1 {
  background-color: var(--color-4);
  border-radius: 100%;
  width: 60px;
  padding: 4px;
}

h1 {
  font-family: Quicksand, monospace;
  font-weight: 200;
}

.graph-stats {
  color: #000;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.model3d {
  color: #fff;
  background-color: #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button-28 {
  appearance: none;
  background-color: var(--color-5);
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  will-change: transform;
  border: 2px solid #1a1a1a;
  outline: none;
  width: 100%;
  min-width: 0;
  min-height: 60px;
  margin: 0;
  padding: 16px 24px;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  text-decoration: none;
  transition: all .3s cubic-bezier(.23, 1, .32, 1);
  display: inline-block;
}

.button-28:disabled {
  pointer-events: none;
}

.button-28:hover {
  color: #fff;
  background-color: #1a1a1a;
  transform: translateY(-2px);
  box-shadow: 0 8px 15px #00000040;
}

.button-28:active {
  box-shadow: none;
  transform: translateY(0);
}

.project-status-altitude {
  color: #fff;
  align-items: center;
  gap: 20px;
  font-size: 1.2rem;
  display: flex;
}

.project-status-altitude p {
  font-family: monospace;
}

.stats-area {
  color: #000;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 50px;
  display: flex;
}
